section {
  flex-direction: column;
  display: flex;
}

section .navi_section {
  flex-direction: row;
  margin-left: 10em;
  display: flex;
}

section .navi_section_item {
  margin: 20px;
}

section .navi_section_item a {
  color: #000;
  text-decoration: none;
}

section .navi_section_item a:hover {
