  display: flex;
}

section .item_list .cart_item .product_price {
  width: 140px;
  font-family: Bebas Neue, cursive;
  font-size: 32px;
  font-weight: lighter;
}

section .item_list .cart_item .product_name {
  width: 220px;
  font-size: 48px;
  line-height: 20px;
}

section .item_list .product_image {
  width: 200px;
  height: 400px;
}

section .total_price_div {
  flex-direction: row;
  margin-left: 10em;
  display: flex;
}

section .total_price_div h2 {
  margin: 15px;
}

section .checkout_button {
  margin-top: 2em;
  margin-bottom: 5em;
  margin-left: 11em;
}

section .checkout_button .checkout_button_a {
  color: #ffe70c;
  background-color: #000;
  border: none;
  border-radius: 10px;
