.product_price {
  width: 140px;
  font-family: Bebas Neue, cursive;
  font-size: 32px;
  font-weight: lighter;
}

.product_name {
  width: 100%;
  font-size: 48px;
  line-height: 40px;
}

.product_delete {
  width: 50px;
  height: 50px;
  color: #ffe70c;
  background-color: #000;
  border: none;
  border-radius: 100%;
}

.product_delete:hover {
  color: #000;
  cursor: pointer;
  background-color: #fff;
}

footer .footer_section_1 {
  background-color: #f1f1f1;
